olution:192dpi)';\n","@use '../abstracts/variables';\n@use '../base/typography';\n\n.u-center-text {\n  text-align: center;\n}\n\n.u-margin-bottom-8 {\n  margin-bottom: typography.rem(128px);\n}\n","@use '../base/typography';\n\n.btn {\n  &:link,\n  &:visited {\n    position: relative;\n    display: inline-block;\n    padding: typography.em(16px) typography.em(32px);\n    border-radius: typography.em(16px);\n    box-shadow: #0000003d 0 0.1875em 0.5em;\n    font-size: clamp(typography.rem(16px), 1.1111vw, typography.rem(32px));\n    text-decoration: none;\n    text-transform: uppercase;\n  }\n\n  &:active {\n    box-shadow: #63636333 0 0.125em 0.5em 0;\n    transform: translateY(-0.0625em);\n    transform: scale(0.98);\n  }\n\n  &::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: -1;\n    display: inline-block;\n    width: 100%;\n    height: 100%;\n    border-radius: 1em;\n    transition: all 0.4s;\n  }\n\n  &:hover {\n    transform: translateY(-0.1875em);\n    transform: scale(1.03);\n\n    &::after {\n      transform: scaleX(1.5) scaleY(1.7);\n      opacity: 0;\n    }\n  }\n\n  &-light {\n    margin: 0 auto auto;\n    background-color: #fff;\n    color: #777;\n\n    &::after {\n      background-color: #fff;\n    }\n  }\n\n  &-animated {\n    animation: moveInBottom 0.5s ease-out 0.75s;\n    animation-fill-mode: backwards;\n  }\n}\n","@use '../abstracts/variables';\n@use '../base/typography';\n\n\n.header {\n  position: relative;\n  height: 95vh;\n  // background-image:\n  // linear-gradient(\n  //   to right bottom,\n  //   rgb(variables.$color-primary--light, 0.702),\n  //   rgb(variables.$color-primary--dark, 0.702)\n  // ), url('/img/hero-2000.jpeg');\n  // background-image:\n  //   linear-gradient(\n  //     to right bottom,\n  //     rgb(variables.$color-primary--light, 0.702),\n  //     rgb(variables.$color-primary--dark, 0.702)\n  //   ), url('/img/hero-2000-v2.webp');\n  // background-position: bottom left;\n  // b